.btn {
  display: flex;
  border: 1px solid black;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  min-height: 50px;
  max-width: 190px;
  color: black;
  margin-top: 30px;
  transition: all 0.2s linear;
}
.btn:hover {
  background-color: rgb(27, 27, 27);
  color: white;
}